.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0,0.4);


  &__headers {
    margin-top: 100px;
    margin-bottom: 40px;
    font-size: 20px;
    ul {
      list-style-type: square; } }

  &__section {
    font-size: 24px;
    margin: 20px auto;
    text-align: justify; } }


/* Modal Content/Box */
.modal-content {
  background-color: #F9773A;
  margin: 10% auto;
  padding: 20px;
  border: 1px solid #F06A3D;
  // width: 80%
  width: 60%;
  min-height: 60%;

  @include r(900) {
    width: 80%; }

  // +rmin(1500)
 }  //   width: 60%

.modal-wrapper {
  width: 100%;
  margin: 20px auto;
  position: relative;
  content: " "; }

.modal__title {
  @include vcenter;
  white-space: nowrap;
  &:hover {
  		text-shadow: -1px -1px 0 #000,1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
  		color: #F9773A;
  		opacity: 0.5; } }

.modal__content {
  max-width: 80%;
  margin: 20px auto; }


/* The Close Button */
.close {
  color: #000;
  float: right;
  // font-size: 28px
  font-size: 20px;
  font-weight: bold; }


.close:hover,
.close:focus {
  color: #fff;
  text-decoration: none;
  cursor: pointer; }
