.loader {
	width: 100%;
	height: 100%;
	min-width: 100vw;
	min-height: 100vh;
	font-size: 64px;
	position: fixed;
	z-index: $z_loader;
	background-color: #F9773A;

	transition: opacity ease-out .5s; }

.loader-wrapper {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%); }

.loaders {


	@for $i from 1 through 6 {
		&__title:nth-child(#{$i}) {
			&::before {
				animation: jump 3s steps(1, end) #{$i*0.5s} infinite; } } }

	&__title {
		position: relative;
		margin-bottom: 30px;

		&::before {
			content: attr(data-content);
			opacity: 0;
			position: absolute;
			transform: translate(5%, 21%);
 } } }			// animation: jump 0.5s infinite

@keyframes jump {
	0% {
		opacity: 0; }
	1% {
		opacity: 1; }
	20% {
		opacity: 0; }
	50% {
		opacity: 0; }
	70% {
		opacity: 0; }
	90% {
		opacity: 0; }
	100% {
		opacity: 0; } }
