.contact {
	padding-top: 100px;
	max-width: 960px;
	margin: auto;
	margin-bottom: 100px;

	@include r(600) {
		padding-top: 0;
		margin-bottom: 0; } }

.contact-wrapper {
	padding-top: 60px;
	display: flex;
	justify-content: space-around;

	@include r(900) {
		flex-direction: column;
		align-items: center;
		margin: auto; }

 }	// align-items: baseline


.mails-wrapper {

 }	// position: relative

.mails {
	.mails__mail {
		margin-top: -25px;
		@include r(900) {
			margin-top: 0; }

		&:first-child {
			margin-top: 0; }

		a {
			font-size: 36px;
			text-decoration: none;
			color: #000;

			@include r(600) {
				font-size: 28px; }

			&:hover {
				color: #fff;
 } } } }				// text-decoration: underline

