.about {
	padding-top: 140px;
	position: relative; }


.content {
	padding-top: 60px;
	max-width: 900px;
	margin: auto;
	text-align: justify;
	font-size: 36px;



	&__damaged {
		font-size: 0.7em;

		@include r(900) {
			font-size: 0.6em; }

		@include r(600) {
			font-size: 0.3em; } }
	// font-size: 0.75em

	@include r(900) {
		max-width: 700px; }

	@include r(600) {
		max-width: 350px;
		font-size: 24px; } }

.damaged__error {
	@include r(900) {
		font-size: 0.7em; }

	@include r(600) {
		font-size: 0.5em; } }


.goals {
	display: flex;
	justify-content: space-between;
	// margin: 50px auto
	padding: 2%;

	// &:hover
	// 	border: solid black 1px

	&__goal {
		max-width: 45%;
		text-align: left; } }

.goals-wrapper {
	font-size: 0.5em; }

.damaged-wrapper {
	font-size: 0.7em; }

.damaged {
	&--center {
		text-align: center; }
	&__dots {
		display: flex;
		justify-content: space-between; }

	&__error {} }



.header-wrapper {
	text-align: center;
	@include vcenter; }


@keyframes blink {
	0% {
		transform: translate(0, 0);
		opacity: 0; }
	10% {
		transform: translate(1%, 25%);
		opacity: 1; }
	15% {
		transform: translate(0,0);
		opacity: 0; }
	90% {
		transform: translate(1%, 25%);
		opacity: 1; }
	95% {
		transform: translate(0,0);
		opacity: 0; } }

