
.hero, .hero-bg {
	width: 100%;
	height: 100%;
	min-height: 100vh;
	min-width: 100vw; }

.hero-bg {
	// opacity: 0.2
	opacity: 0;
	background-image: url("../img/iv_bertona.jpg");
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center; }

.hero {
	position: relative; }

.hero-wrap {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	width: 100%;


	&__title {
		position: relative;
		text-align: center;
		display: flex;
		justify-content: center;
		margin: auto;
		max-width: 80%;
 }		// max-width: 74%

	&__letter {
		opacity: .2;
		min-width: 30px;

		@include r(600) {
			min-width: 10px; } }


	&--stroke {
		text-shadow: -1px -1px 0 #000,1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
		// text-shadow: -2px -2px 0 #000,2px -2px 0 #000, -2px 2px 0 #000, 2px 2px 0 #000
		color: #F9773A;
		letter-spacing: 2px; } }

.borders-wrapper {
	position: absolute;
	width: 100%;
	height: 100%;
	overflow: hidden;
	z-index: -1;

	&__border {
		position: absolute;
		content: " ";
		transition: transform  ease-out 0.25s;

		&:first-child {
			background-color: #fff;
			width: 1px;
			top: 0;
			bottom: 0;
			left: 0;
			transform: translateY(-100%); }

		&:nth-child(2) {
			background-color: #fff;
			height: 1px;
			top: 0;
			left: 0;
			right: 0;
			transform: translateX(-100%); }

		&:nth-child(3) {
			background-color: #fff;
			width: 1px;
			top: 0;
			bottom: 0;
			right: 0;
			transform: translateY(-200%); }


		&:nth-child(4) {
			background-color: #fff;
			height: 1px;
			left: 0;
			bottom: 0;
			right: 0;
			transform: translateX(-200%); } } }



@keyframes intro {
	0% {
		opacity: 1; }
	20% {
		transform: translateY(20%); }
	29% {
		opacity: 0; }
	30% {
		opacity: 1;
		transform: rotate(180deg); }
	40% {
		opacity: 1;
		transform: translateY(0); }
	65% {
		opacity: 1;
		transform: rotate(180deg) translateY(10%); }
	90% {
		opacity: 1;
		transform: rotate(0); }
	92% {
		opacity: 0; }
	100% {
		opacity: 0; } }
