.technology-wrapper {
	margin-top: 140px;
	display: flex;
	justify-content: space-around;
	//
	//replace to em
	// font-size: 48px
	font-size: 32px;
	position: relative;

	@include r(740) {
		flex-direction: column;
		align-items: center;
		margin-top: 80px; } }

.front-end, .back-end {
	display: flex;
	white-space: nowrap;
	user-select: none;
	position: relative;
	// min-width: 50%
	margin: auto;
	// text-align: center

	@include r(740) {
		min-width: 100%;
		justify-content: center; } }


.bg--tx {
	opacity: .1; }

.technology-content {
	@include vcenter;
	user-select: text;
	// cursor: pointer

	&__list {
		li {
			text-align: center;

			@include r(500) {
				text-align: left; }
			//
			// &:hover
 } } }			// 	color: #fff


.technology-header {
	// display: none
	text-align: center;
	position: absolute;
	top: -2%;
	left: 50%;
	transform: translate(-50%, 3%);

	@include r(900) {
		h5 {
			display: none; } } }
