body {
	background: #F9773A;
	// background: #FD743E
	// background: rgb(253, 105, 44)
	width: 100%;
	overflow-x: hidden;
	@include css-lock(16, 50, 600, 1000);
	line-height: 1.4;
	font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
	text-transform: uppercase;
	text-rendering: geometricPrecision; }

.main {
	opacity: 0;
	transition: opacity ease-in 1s; }

.video-container {
	position: absolute;
	width: 100%;
	height: 100%; }



.video-bg {
	position: absolute;
	top: 0;
	left: 0;
	// height: 100%
	width: 100%;
	@include r(1200) {
		width: 100%;
		height: auto;
		top: 50%;
		transform: translateY(-50%); } }


.hidde {
	opacity: 0; }

.no--select {
	user-select: none; }

.no-display {
	// display: none
	z-index: -1; }
.visible {
	opacity: 1; }

.text--upper {
	text-transform: uppercase; }

.text--normal {
	text-transform: none; }

.text--crossed {
	 text-decoration: line-through;
	 opacity: 0.8; }

.text--error {
	white-space: nowrap; }

.text--blink {
	position: relative;

	&::before {
		content: attr(data-content);
		position: absolute;
		left: 0;
		top: 0;
		animation: blink;
		animation-duration: 7s;
		animation-timing-function: step-end;
		animation-iteration-count: infinite;
		// animation-delay: 5s
		opacity: 0; }


	&:hover {
		&::before {
			top: 25%;
			left: 1%;
			// animation-play-state: paused
			opacity: 1; } } }

.text--stroke {
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
  color: #f9773a;
  opacity: .5;

		&::first-letter {
			color: #000;
 } }			// opacity: 1

.text--hover-white {
	&:hover {
		color: white;
		cursor: pointer; } }

.text--light {
	font-weight: 100; }

.text--regular {
	font-weight: 400; }

.text--bold {
	font-weight: bold; }

.text--color-wthite {
	color: #fff; }

.text--border-on {
	padding: 1%;
	border: dashed black 1px; }

.section-header {
	text-transform: uppercase;
	position: relative;
	width: 60%;
	margin: auto;
	height: 100%;
	min-height: 30vh; }


.titles-wrapper {
	position: relative;
	width: 100%;
	height: 100%;

	min-width: 100vw;
	min-height: 30vh;

	&__b-title {
		@include vcenter;
		user-select: none;

		&:hover {
			text-shadow: -1px -1px 0 #000,1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
			color: #F9773A;
			opacity: 0.5; }

		@include r(700) {
			font-size: 36px; } }


	&__f-title {
		position: absolute;
		font-size: 48px;

		&:hover {
			text-shadow: -1px -1px 0 #fff,1px -1px 0 #fff, -1px 1px 0 #fff, 1px 1px 0 #fff;
			color: #F9773A;
			opacity: 0.9; }

		@include r(700) {
			font-size: 36px; }

		@include r(600) {
			font-size: 24px; }

		&:nth-child(2) {
			left: 25%;
			top: 10%;

			@include r(900) {
				left: 15%; } }

		&:nth-child(3) {
			left: 25%;
			top: 50%;

			@include r(900) {
				left: 15%; } }

		&:nth-child(4) {
			right: 25%;
			top: 10%;

			@include r(900) {
				right: 15%; } }

		&:nth-child(5) {
			right: 25%;
			top: 50%;

			@include r(900) {
				right: 15%; } }

		&:nth-child(6) {
			// margin-top: -3%
			// display: none
			left: 43%;
			top: 10%;

			@include r(1600) {
				display: none; } }

		&:nth-child(7) {
			// display: none
			// margin-top: 3%
			left: 43%;
			top: 50%;

			@include r(1600) {
				display: none; } } } }

.footer {
	margin: auto;
	width: 100%;
	margin-bottom: 2%;

	p {
		text-align: center;
		font-size: 24px; } }
