.portfolio {
  margin: 100px auto;
  width: 100%;
  overflow: visible;
  position: relative;
  position: relative;

  &__link {
    position: absolute; }

  &-wrapper {
    margin: auto;
    position: relative;


    &:hover {
      cursor: default; } } }

.portfolio-info {
  position: absolute;
  opacity: 0;
  z-index: -1;
  // left: 30%
  top: 0;
  left: 50%;
  transform: translateX(-50%); }

.info-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0);
  color: #fff;
  overflow: hidden; }


.info--column {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }

.info__work-wrapper {
  position: absolute;
  bottom: 0;
  left: 10%;
  transform: translate(-10%, 0);
  // color: #000
  color: #fff;


  // +r(1200)
  //   color: #fff
  //   mix-blend-mode: exclusion


  p {
    font-size: 14px; } }


.info__title-wrapper {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  // color: #000
  color: #fff;

  @include r(900) {
    color: #fff; }


  h4 {
    font-size: 24px; }

  a {
    padding-top: 20px;
    font-size: 20px;
    text-decoration: none;
    color: #FFF;
    // color: #000

    @include r(600) {
      color: #fff; }

    &:hover {
      border-bottom: 1px solid #fff;
      color: #fff; } } }



.portfolio-links {
  margin: 70px 0;
  min-height: 80px;
  height: 100%;
  white-space: nowrap;



  &:hover {
    color: #fff;
    cursor: default; } }
